<template>
  <div id="notice">
    <el-table
        border
        height="65vh"
        stripe
        :data="meetings"
        style="width: 100%">
      <el-table-column
          prop="meetingName"
          label=""
      >
      </el-table-column>
      <el-table-column
          prop="roomName"
          label="">
      </el-table-column>
      <el-table-column
          prop="Time"
          label="">
      </el-table-column>
      <el-table-column
          prop="personNum"
          label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <div v-if="(Date.parse(scope.row.startTime) - nowTime)<=1800000">
            <img  src="../static/normal.png" alt="">
          </div>
          <div v-if="(Date.parse(scope.row.startTime) - nowTime)>1800000">
            <img  src="../static/error.png" alt="">
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div class="text">:30</div>
  </div>
</template>

<script>
export default {
  name: 'myMeeting',
  data() {
    return {
      nowTime:'',
      meetings: []
    }
  },
  methods: {
    handleDelete(index, row) {
      this.$confirm ('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then (() => {
            this.axios.post('/meeting/delete',{meid:row.meid})
                .then(res=>{
                  console.log (res)
                  if (res.data.state != false) {
                    this.meetings.splice(index,1)
                    this.$message.success ('')
                  }
                })
                .catch(error=>{
                  this.$message.error(error + ",")
                })
          }
      )
          .catch (() => {
            this.$message ({
              type: 'info',
              message: ''
            });
          });
    }
  },
  created() {
    this.nowTime = Date.parse(new Date())
    this.axios.get('/meeting/getAllMeetings')
        .then(res=>{
          console.log (res.data)
          this.meetings = res.data.meetings
          for (let i = 0;i<this.meetings.length ;i++){
            this.meetings[i].Time = this.meetings[i].startTime.substring(0,10) + '\n' + this.meetings[i].startTime.substring(11,16) + '-' +  this.meetings[i].endTime.substring(11,16)
          }
        })
        .catch(error=>{
          this.$message.error(error + ',,')
        })
  }
}
</script>

<style scoped>
#notice .name-wrapper{
  display: inline-block;
}
.upload{
  display: inline-block;
  margin-left: 0.5vw;
  position: relative;
  top: 0.8vh;
}
.upload:hover{
  box-shadow: skyblue 0 0 10px;
  transition: 0.5s;
}
.upload img{
  display: inline-block;
  width: 20px;
  height: 20px;
}
#notice img{
  display: inline-block;
  width: 30px;
  height: 30px;
  position: relative;
  top: 8px;
}
#notice .text{
  color: blue;
  position: absolute;
  left: 25vw;
}
</style>

<style>
.el-tag--medium{
  display: inline-block !important;
}
.cell{
  font-weight: bolder !important;
  color: black !important;
  text-align: center !important;
}
.el-table{
  width: 80% !important;
  margin-left: 10vw;
  margin-top: 5vh;
  box-shadow: 0 0 10px skyblue;
}
.is-leaf,
.gutter{
  background: #FAFAFA !important;
}

.el-table .cell {
  white-space: pre-line !important;
}
</style>